import React, { Component } from "react";

import { EditorView } from "prosemirror-view";
import { initState, docToJson } from "./helpers";
import { menu } from "./menu";

export class ProseMirrorWriter extends Component {
  componentDidMount() {
    const state = initState(this.props.file.content.doc, [menu]);

    this.editorView = new EditorView(this.editor, {
      state,
      dispatchTransaction: transaction => {
        const newState = this.editorView.state.apply(transaction);

        const doc = docToJson(newState.doc);

        this.props.onChange && this.props.onChange({ doc });

        this.editorView.updateState(newState);
      },
      handleDOMEvents: {
        blur: (view, e) => {
          const doc = docToJson(view.state.doc);

          this.props.onBlur && this.props.onBlur({ doc });
        },

        focus: (view, e) => {
          const doc = docToJson(view.state.doc);

          this.props.onFocus && this.props.onFocus({ doc });
        }
      }
    });

    if (this.props.focusOnMount) {
      this.focus();
    }
  }

  focus() {
    this.editorView.focus();
  }

  render() {
    return (
      <div className="ProseMirrorWriter-editor-and-toolbar-container">
        <div
          className="ProseMirrorWriter-toolbar-container"
          id="proseMirrorWriterToolbarContainer"
        />
        <div
          className="ProseMirrorWriter-editor-container"
          ref={el => (this.editor = el)}
        />
      </div>
    );
  }
}



