import React, { Component } from "react";
import { SnippetView } from "../layout/SnippetView";
import { Modal } from "./Modal";
import "./CreateSnippetModal.css";

export class CreateSnippetModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isAdding: false,
      initialSnippet: props.initialSnippet,
      snippet: null
    };

    this.onSave = this.onSave.bind(this);
    this.onBuffer = this.onBuffer.bind(this);
    this.onClose = this.onClose.bind(this);
  }

  onSave() {
    this.props.createSnippet(this.state.snippet);
    this.close();
  }

  onClose() {
    this.close();
  }

  close() {
    this.props.onClose && this.props.onClose();
  }

  onBuffer(snippet) {
    this.setState({ snippet });
  }

  render() {
    return (
      <Modal onClose={this.onClose}>
        <div className="CreateSnippetModal">
          <SnippetView
            onSave={this.onBuffer}
            snippet={this.state.initialSnippet}
            allowReadMode={false}
            focusOnMount={true}
          />
          <button
            className="CreateSnippetModal-save-button"
            onClick={this.onSave}
          >
            save
          </button>
        </div>
      </Modal>
    );
  }
}



