import React, { Component } from "react";
import "./App.css";
import { CreateSnippetModal } from "./ui/CreateSnippetModal";
import { Content } from "./layout/Content";
import { QuickSearch } from "./QuickSearch";
import { SnippetLayout } from "./layout/SnippetLayout";
import { ClassicSnippetLayout } from "./layout/ClassicSnippetLayout";
import { Sidebar } from "./sidebar/Sidebar";
import { Topbar } from "./sidebar/Topbar";
import {
  aggregateTags,
  replaceItemInListById,
  searchSnippet,
  dropItemInListById,
  bootstrapSnippet
} from "./helpers";
import { Backend, setCreationDate, sortPredicate } from "./Backend";
import { UserSession } from "./UserSession";

const initialState = {
  currentlyLoadingSnippetId: null,
  currentSnippetOperation: null,
  snippets: [],
  tags: [],
  query: "",
  isDoingQuickSearch: false,
  layout: 0,
  isAddingNew: false,
  initialValueForCreateModal: "",
  createSnippetExpanded: false,
  topbarExpanded: false,
  selectedTags: []
};

class App extends Component {
  constructor() {
    super();

    this.state = initialState;
    this.createSnippet = this.createSnippet.bind(this);
    this.updateSnippet = this.updateSnippet.bind(this);
    this.onChangeSearchQuery = this.onChangeSearchQuery.bind(this);
    this.onConfirmSearchQuery = this.onConfirmSearchQuery.bind(this);
    this.onStartAddingNew = this.onStartAddingNew.bind(this);
    this.onStopAddingNew = this.onStopAddingNew.bind(this);
    this.handleGlobalKeydown = this.handleGlobalKeydown.bind(this);
    this.closeQuickSearch = this.closeQuickSearch.bind(this);
    this.toggleCreateSnippetSidebar = this.toggleCreateSnippetSidebar.bind(
      this
    );
    this.onBlurExpandableSidebar = this.onBlurExpandableSidebar.bind(this);
    this.toggleTopbar = this.toggleTopbar.bind(this);
    this.onBlurTopbar = this.onBlurTopbar.bind(this);
    this.onEscapeTopbar = this.onEscapeTopbar.bind(this);
    this.toggleTagsSidebar = this.toggleTagsSidebar.bind(this);
    this.onClickTag = this.onClickTag.bind(this);

    document.addEventListener("keydown", this.handleGlobalKeydown);

    this.backend = new Backend();
    this.userSession = new UserSession(this.backend);

    const loadSnippets = user =>
      this.backend
        .loadAllSnippets(user)
        .then(snippets =>
          this.setState({ snippets, tags: aggregateTags(snippets) })
        );

    this.userSession.getUser().then(user => {
      this.user = user;
      this.setState({ layout: (user.settings && user.settings.layout) || 1 });
      loadSnippets(user);
    });
  }

  createSnippet(snippet) {
    const _snippet = setCreationDate(snippet);

    this.setState({
      snippets: this.state.snippets
        .concat({ ..._snippet, id: "new" })
        .sort(sortPredicate),
      currentlyLoadingSnippetId: "new",
      currentSnippetOperation: "create"
    });

    this.backend.createSnippet(_snippet, this.user).then(newSnippet => {
      this.setState(oldState => {
        const snippets = replaceItemInListById(
          oldState.snippets,
          "new",
          newSnippet
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null,
          currentSnippetOperation: null
        };
      });
    });
  }

  updateSnippet(snippet) {
    this.setState({
      currentlyLoadingSnippetId: snippet.id,
      currentSnippetOperation: "update"
    });

    this.backend.updateSnippet(snippet).then(updatedSnippet => {
      this.setState(oldState => {
        const snippets = replaceItemInListById(
          oldState.snippets,
          updatedSnippet.id,
          updatedSnippet
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null,
          currentSnippetOperation: null
        };
      });
    });
  }

  deleteSnippet(snippet) {
    this.setState({
      currentlyLoadingSnippetId: snippet.id,
      currentSnippetOperation: "delete"
    });

    this.backend.deleteSnippet(snippet).then(deletedSnippet => {
      this.setState(oldState => {
        const snippets = dropItemInListById(
          oldState.snippets,
          deletedSnippet.id
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null,
          currentSnippetOperation: null
        };
      });
    });
  }

  onChangeSearchQuery(e) {
    this.setState({ query: e.target.value });
  }

  onConfirmSearchQuery() {
    this.setState({ topbarExpanded: false });
  }

  onStartAddingNew(e, config) {
    let snippet;

    const content = e.target.value || ""; // Target can either be button or textarea

    snippet = bootstrapSnippet("", "", content, config.language);

    this.setState({
      isAddingNew: true,
      initialSnippetForCreateModal: snippet
    });
  }

  onStopAddingNew() {
    this.setState({ isAddingNew: false });
  }

  toggleCreateSnippetSidebar() {
    this.setState(oldState => {
      return {
        createSnippetExpanded: !oldState.createSnippetExpanded,
        tagsExpanded: false
      };
    });
  }

  onBlurExpandableSidebar() {
    this.setState(oldState => {
      return { createSnippetExpanded: false, tagsExpanded: false };
    });
  }

  toggleTopbar() {
    this.setState(oldState => {
      return { topbarExpanded: !oldState.topbarExpanded };
    });
  }

  onBlurTopbar() {
    this.setState(oldState => {
      return { topbarExpanded: false };
    });
  }

  onEscapeTopbar() {
    this.setState(oldState => {
      return { topbarExpanded: false, query: "" };
    });
  }

  closeQuickSearch() {
    this.setState({ isDoingQuickSearch: false });
  }

  toggleTagsSidebar() {
    this.setState(oldState => {
      return {
        tagsExpanded: !oldState.tagsExpanded,
        createSnippetExpanded: false
      };
    });
  }

  onClickTag(tag) {
    this.setState(oldState => {
      if (oldState.selectedTags.includes(tag)) {
        return {
          selectedTags: oldState.selectedTags.filter(_tag => tag !== _tag)
        };
      } else {
        return { selectedTags: oldState.selectedTags.concat([tag]) };
      }
    });
  }

  handleGlobalKeydown(e) {
    if (e.target.nodeName === "BODY") {
      if (e.keyCode === 75) {
        e.preventDefault();

        this.setState({ isDoingQuickSearch: true });
      } else if (e.keyCode === 78) {
        e.preventDefault();

        this.setState({ createSnippetExpanded: true });
      } else if (e.keyCode === 70) {
        e.preventDefault();

        this.toggleTopbar();
      }
    }
  }

  setLayout(layout) {
    this.setState({ layout }, () =>
      this.backend.updateUser({
        ...this.user,
        settings: { ...this.user.settings, layout }
      })
    );
  }

  render() {
    const filteredSnippets = this.state.snippets
      .filter(snippet => searchSnippet(snippet, this.state.query))
      .filter(
        snippet =>
          this.state.selectedTags.length > 0
            ? snippet.tags.some(tag =>
                this.state.selectedTags.some(_tag => tag === _tag)
              )
            : true
      );

    const listLayout = (
      <SnippetLayout
        snippets={filteredSnippets}
        onClickDelete={this.deleteSnippet.bind(this)}
        currentlyLoadingSnippetId={this.state.currentlyLoadingSnippetId}
        currentSnippetOperation={this.state.currentSnippetOperation}
        onSave={this.updateSnippet}
      />
    );

    const classicLayout = (
      <ClassicSnippetLayout
        snippets={filteredSnippets}
        onClickDelete={this.deleteSnippet.bind(this)}
        currentlyLoadingSnippetId={this.state.currentlyLoadingSnippetId}
        currentSnippetOperation={this.state.currentSnippetOperation}
        onSave={this.updateSnippet}
      />
    );

    const layout = this.state.layout === 0 ? classicLayout : listLayout;

    const quickSearch = this.state.isDoingQuickSearch && (
      <QuickSearch
        snippets={this.state.snippets}
        onClose={this.closeQuickSearch}
      />
    );

    const classes = ["Main", "App"];

    if (this.state.createSnippetExpanded) {
      classes.push("App-expandable-sidebar-create-snippet-expanded");
    }

    if (this.state.tagsExpanded) {
      classes.push("App-expandable-sidebar-tags-expanded");
    }

    if (this.state.topbarExpanded) {
      classes.push("App-topbar-expanded");
    }

    if (this.state.isAddingNew || this.state.isDoingQuickSearch) {
      classes.push("App-create-modal-expanded");
    }

    return (
      <div className={classes.join(" ")}>
        <div className="App-blurrables">
          <Sidebar
            createSnippetExpanded={this.state.createSnippetExpanded}
            tagsExpanded={this.state.tagsExpanded}
            onClickAdd={this.toggleCreateSnippetSidebar}
            setLayout={this.setLayout.bind(this)}
            onStartAddingNew={this.onStartAddingNew}
            onBlur={this.onBlurExpandableSidebar}
            onEscape={this.onBlurExpandableSidebar}
            onClickSearch={this.toggleTopbar}
            onClickTags={this.toggleTagsSidebar}
            onClickTag={this.onClickTag}
            tags={this.state.tags}
            selectedTags={this.state.selectedTags}
          />

          <Topbar
            expanded={this.state.topbarExpanded}
            onBlur={this.onBlurTopbar}
            onEscape={this.onEscapeTopbar}
            onChangeSearchQuery={this.onChangeSearchQuery}
            onEnter={this.onConfirmSearchQuery}
            value={this.state.query}
          >
            a
          </Topbar>

          <Content>{layout}</Content>
        </div>

        {this.state.isAddingNew && (
          <CreateSnippetModal
            createSnippet={this.createSnippet}
            onClose={this.onStopAddingNew}
            initialSnippet={this.state.initialSnippetForCreateModal}
          />
        )}

        {quickSearch}
      </div>
    );
  }
}

export default App;



