import React, { Component } from "react";

import { CodeMirrorWriter } from "./CodeMirror/Writer";
import { CodeMirrorReader } from "./CodeMirror/Reader";
import { ProseMirrorWriter } from "./ProseMirror/Writer";
import { ProseMirrorReader } from "./ProseMirror/Reader";
import { TodoListReader } from "./TodoList/Reader";
import { TodoListWriter } from "./TodoList/Writer";

export const ProseMirrorReadOrWrite = composeReadOrWriteEditor(
  ProseMirrorReader,
  ProseMirrorWriter
);

export const CodeMirrorReadOrWrite = composeReadOrWriteEditor(
  CodeMirrorReader,
  CodeMirrorWriter
);

export const TodoListReadOrWrite = composeReadOrWriteEditor(
  TodoListReader,
  TodoListWriter
);

function composeReadOrWriteEditor(Reader, Writer) {
  return class extends Component {
    constructor(props) {
      super(props);

      this.onStartEdit = this.onStartEdit.bind(this);
      this.onStopEdit = this.onStopEdit.bind(this);
      this.onBlur = this.onBlur.bind(this);
      this.onFocus = this.onFocus.bind(this);

      const isEditing = false;

      this.state = { isEditing };

      this.props.onChangeHasFocus(isEditing);
    }

    onStartEdit() {
      this.setState({ isEditing: true });
    }

    onStopEdit() {
      this.setState({ isEditing: false });
    }

    onBlur(content) {
      if (this.props.allowReadMode) {
        this.onStopEdit();
      }

      this.props.onChangeHasFocus(false);

      this.props.onBlur(content);
    }

    onFocus(content) {
      this.props.onChangeHasFocus(true);
    }

    renderWriter() {
      return (
        <Writer
          {...this.props}
          onBlur={this.onBlur}
          onFocus={this.onFocus}
          onSuggestPersist={this.props.onSuggestPersist}
          onChange={this.props.onChange}
          ref={ref => (this.editor = ref)}
          focusOnMount={this.props.focusOnMount}
        />
      );
    }

    renderReader() {
      return (
        <div onClick={this.onStartEdit}>
          <Reader {...this.props} />
        </div>
      );
    }

    render() {
      if (this.props.allowReadMode) {
        return this.state.isEditing ? this.renderWriter() : this.renderReader();
      } else {
        return this.renderWriter();
      }
    }
  };
}



