import React, { Component } from "react";
import { SnippetEditor } from "../editor/SnippetEditor";
import "./SnippetView.css";

export class SnippetView extends Component {
  render() {
    return (
      <SnippetEditor
        snippet={this.props.snippet}
        onSave={this.props.onSave}
        upperRightCorner={this.props.upperRightCorner}
        allowReadMode={this.props.allowReadMode}
        focusOnMount={this.props.focusOnMount}
      />
    );
  }
}



