export function getSnippetTitle(snippet) {
  if (snippet.type === undefined || snippet.type === "textual") {
    return snippet.title || snippet.files[0].name;
  } else if (snippet.type === "todo") {
    return snippet.title;
  }
  return undefined;
}

// TODO reuse this in the sidebar for the icons in the buttons
export function getSnippetIcon(snippet) {
  if (snippet.type === undefined || snippet.type === "textual") {
    return snippet.files[0].language === "richtext" ? "note" : "code";
  } else if (snippet.type === "todo") {
    return "assignment_turned_in";
  }
  return undefined;
}

export function getSnippetClass(snippet) {
  if (snippet.type === undefined || snippet.type === "textual") {
    return snippet.files[0].language === "richtext"
      ? "ClassicSnippetLayout-snippet-list-entry-type-richtext"
      : "ClassicSnippetLayout-snippet-list-entry-type-other";
  } else if (snippet.type === "todo") {
    return "ClassicSnippetLayout-snippet-list-entry-type-todo";
  }
  return undefined;
}



