import data from "./initial-snippets.json";

export function setCreationDate(snippet) {
  return snippet.creationDate
    ? snippet
    : { ...snippet, creationDate: Date.now() };
}

export const sortPredicate = (a, b) =>
  (b.creationDate || 0) - (a.creationDate || 0);

function deleteId(item) {
  const _item = { ...item };
  delete _item.id;
  return item;
}

export class Backend {
  constructor() {
    this.db = window.firebase.firestore();
    this.db.settings({ timestampsInSnapshots: true });
  }

  loadAllSnippets(user) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .where("userId", "==", user.id)
        .get()
        .then(result => {
          const snippets = [];
          result.forEach(doc => snippets.push({ ...doc.data(), id: doc.id }));

          resolve(snippets.sort(sortPredicate));
        });
    });
  }

  createSnippet(snippet, user) {
    const _snippet = setCreationDate(snippet);

    _snippet.userId = user.id;

    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .add(_snippet)
        .then(doc => {
          resolve({ ..._snippet, id: doc.id });
        });
    });
  }

  updateSnippet(snippet) {
    const _snippet = deleteId(snippet);

    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .doc(snippet.id)
        .set(_snippet)
        .then(() => {
          resolve(snippet);
        });
    });
  }

  deleteSnippet(snippet) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .doc(snippet.id)
        .delete()
        .then(() => {
          resolve(snippet);
        });
    });
  }

  async createInitialSnippets(user) {
    await this.createSnippet(data[0], user);
    await this.createSnippet(data[1], user);
    await this.createSnippet(data[3], user);
    return this.createSnippet(data[2], user);
  }

  createUser() {
    const user = setCreationDate({});

    return new Promise((resolve, reject) => {
      this.db
        .collection("users")
        .add(user)
        .then(doc => {
          const userWithId = { ...user, id: doc.id };

          this.createInitialSnippets(userWithId).then(() => {
            resolve(userWithId);
          });
        });
    });
  }

  getUser(id) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("users")
        .doc(id)
        .get()
        .then(doc => {
          if (doc.exists) {
            resolve({ ...doc.data(), id: doc.id });
          } else {
            reject();
          }
        });
    });
  }

  updateUser(user) {
    const _user = deleteId(user);

    return new Promise((resolve, reject) => {
      this.db
        .collection("users")
        .doc(user.id)
        .set(_user)
        .then(() => {
          resolve(user);
        });
    });
  }
}



