import React, { Component } from "react";
import { SnippetView } from "./SnippetView";
import { getReaderForSnippet } from "../editor/helpers";
import "./ClassicSnippetLayout.css";
import { getSnippetTitle, getSnippetIcon, getSnippetClass } from "./helpers";
import { ClearButton } from "../ui/Buttons";

const isCreatingOrDeleting = currentSnippetOperation =>
  currentSnippetOperation === "create" || currentSnippetOperation === "delete";

const isUpdating = currentSnippetOperation =>
  currentSnippetOperation === "update";

export class ClassicSnippetLayout extends Component {
  constructor() {
    super();

    this.onSave = this.onSave.bind(this);

    this.state = {
      currentlySelectedSnippetIndex: 0
    };
  }

  componentDidUpdate(oldProps) {
    if (
      this.props.currentlyLoadingSnippetId === "new" &&
      this.state.currentlySelectedSnippetIndex !== 0
    ) {
      this.setState({ currentlySelectedSnippetIndex: 0 });
    }
  }

  onSave(snippet) {
    this.props.onSave(snippet);
  }

  onSelectSnippet(index) {
    this.setState({ currentlySelectedSnippetIndex: index });
  }

  onClickDelete(snippet, e) {
    e.preventDefault(); // avoid onBlur on snippet editor and therefor avoid onSave from being called
    this.props.onClickDelete(snippet);
  }

  render() {
    const deleteButton = snippet => (
      <ClearButton onMouseDown={this.onClickDelete.bind(this, snippet)} />
    );

    const updateLoadingIndicator = snippet =>
      this.props.currentlyLoadingSnippetId === snippet.id &&
      (isUpdating(this.props.currentSnippetOperation) ||
        isCreatingOrDeleting(this.props.currentSnippetOperation)) && (
        <span className="SnippetLayout-spinner">
          <i className="SnippetLayout-rotating material-icons Buttons-15">
            autorenew
          </i>
        </span>
      );

    const upperRightCorner = snippet => (
      <span>{updateLoadingIndicator(snippet) || deleteButton(snippet)}</span>
    );

    const renderSnippetListEntryTitle = snippet =>
      getSnippetTitle(snippet) || (
        <span className="ClassicSnippetLayout-snippet-list-entry-untitled">
          untitled
        </span>
      );

    const renderSnippetListEntryContent = snippet => {
      return getReaderForSnippet(snippet);
    };

    const renderSnippetListEntry = snippet => {
      const title = renderSnippetListEntryTitle(snippet);

      return (
        <div>
          {title && (
            <div className="ClassicSnippetLayout-snippet-list-entry-title">
              <i className="material-icons">{getSnippetIcon(snippet)}</i>

              {title}
            </div>
          )}

          <div className="ClassicSnippetLayout-snippet-list-entry-content">
            {renderSnippetListEntryContent(snippet)}
          </div>
        </div>
      );
    };

    const snippets = this.props.snippets.map((snippet, index) => (
      <div
        key={snippet.id}
        className={`ClassicSnippetLayout-snippet-list-entry-outer ${
          index === this.state.currentlySelectedSnippetIndex
            ? "ClassicSnippetLayout-snippet-list-entry-outer-selected"
            : ""
        }`}
      >
        <div
          className={`ClassicSnippetLayout-snippet-list-entry-inner ${getSnippetClass(
            snippet
          )}`}
          onClick={this.onSelectSnippet.bind(this, index)}
        >
          {renderSnippetListEntry(snippet)}
        </div>
      </div>
    ));

    const renderSnippetEditor = snippet => (
      <SnippetView
        key={snippet.id}
        snippet={snippet}
        onSave={this.onSave}
        upperRightCorner={upperRightCorner(snippet)}
        allowReadMode={false}
        focusOnMount={false}
      />
    );

    const currentSnippet = this.props.snippets[
      this.state.currentlySelectedSnippetIndex
    ];

    return (
      <div className="ClassicSnippetLayout">
        <div className="ClassicSnippetLayout-snippet-list">{snippets}</div>
        <div className="ClassicSnippetLayout-editor-view">
          {currentSnippet &&
            renderSnippetEditor(
              this.props.snippets[this.state.currentlySelectedSnippetIndex]
            )}
        </div>
      </div>
    );
  }
}



