import React, { Component } from "react";
import { isStayingWithFocusables } from "../helpers";
import "./AutohidingSidebar.css";

export class AutohidingSidebar extends Component {
  constructor(props) {
    super(props);

    this.handleBlur = this.handleBlur.bind(this);
  }

  componentDidMount() {
    document.addEventListener("focusout", this.handleBlur);
  }

  componentWillUnmount() {
    document.removeEventListener("focusout", this.handleBlur);
  }

  handleBlur(e) {
    if (!isStayingWithFocusables(e, this.props.focusablesPrefix)) {
      this.props.onBlur();
    }
  }

  render() {
    return (
      <div
        className="AutohidingSidebar"
        id={`${this.props.focusablesPrefix}-main`}
        tabIndex="0"
      >
        {this.props.children}
      </div>
    );
  }
}



