import React from "react";

import { ProseMirrorReader } from "./ProseMirror/Reader";
import { CodeMirrorReader } from "./CodeMirror/Reader";
import { TodoListReader } from "./TodoList/Reader";
import { ProseMirrorReadOrWrite } from "./ReadOrWriteEditors";
import { CodeMirrorReadOrWrite } from "./ReadOrWriteEditors";
import { TodoListReadOrWrite } from "./ReadOrWriteEditors";

const defaultMenu = () => <div />;

const ProseMirrorMenu = props => (
  <div className="proseMirrorToolbarContainer" />
);

export function getViewsForFile(file) {
  if (file.language === "richtext") {
    return { Editor: ProseMirrorReadOrWrite, Menu: ProseMirrorMenu };
  } else if (file.language === "todo") {
    return { Editor: TodoListReadOrWrite, Menu: defaultMenu };
  } else {
    return { Editor: CodeMirrorReadOrWrite, Menu: defaultMenu };
  }
}

export function getCssClassFile(file) {
  if (file.language === "richtext") {
    return "SnippetEditor-richtext-mode";
  } else if (file.language === "todo") {
    return "SnippetEditor-todo-mode";
  } else {
    return "";
  }
}

export function getReaderForFile(file) {
  if (file.language === "richtext") {
    return ProseMirrorReader;
  } else if (file.language === "todo") {
    return TodoListReader;
  } else {
    return CodeMirrorReader;
  }
}

export function getReaderForSnippet(snippet) {
  if (snippet.files[0].language === "todo") {
    return (
      <div>
        {snippet.files[0].content.todos.map(({ text }) => text).join(" ")}
      </div>
    );
  } else {
    const Reader = getReaderForFile(snippet.files[0]);
    return <Reader file={snippet.files[0]} trim={true} />;
  }
}

export function shouldComponentUpdateForFile(oldProps, newProps) {
  if (
    oldProps.file.content === newProps.file.content &&
    oldProps.file.language === newProps.file.language &&
    oldProps.trim === newProps.trim
  ) {
    return false;
  }
  return true;
}



