import { addListNodes } from "prosemirror-schema-list";
import { exampleSetup } from "prosemirror-example-setup";
import { schema } from "prosemirror-schema-basic";
import { Schema, DOMParser, Node } from "prosemirror-model";
import { EditorState } from "prosemirror-state";
import "prosemirror-view/style/prosemirror.css";
import "prosemirror-menu/style/menu.css";
import "./styles.css";

export const mySchema = new Schema({
  nodes: addListNodes(schema.spec.nodes, "paragraph block*", "block"),
  marks: schema.spec.marks
});

export function docToJson(doc) {
  return JSON.parse(JSON.stringify(doc.toJSON()));
}

export function docFromString(content) {
  const el = document.createElement("div");
  const _content = content
    .split(/\n/)
    .map(line => `<p>${line}</p>`)
    .join("");

  el.innerHTML = _content;

  return DOMParser.fromSchema(mySchema).parse(el);
}

export function docJsonFromString(content) {
  return docToJson(docFromString(content));
}

export function initDoc(docJson) {
  return Node.fromJSON(mySchema, docJson);
}

export function initState(docJson, plugins) {
  const doc = initDoc(docJson);

  return EditorState.create({
    doc,
    plugins: exampleSetup({ schema: mySchema }).concat(plugins)
  });
}

export function getTextFromDocument(doc) {
  return doc.textBetween(0, doc.content.size, "\n\n");
}



