import React, { Component } from "react";

import * as CodeMirror from "codemirror/lib/codemirror";
import "codemirror/mode/htmlmixed/htmlmixed";
import "codemirror/mode/javascript/javascript";
import "codemirror/lib/codemirror.css";
import "codemirror/theme/mdn-like.css";
import "./styles.css";

import { languageToMode } from "./helpers";

export class CodeMirrorWriter extends Component {
  constructor(props) {
    super(props);

    this.language = this.props.file.language;
  }

  componentDidMount() {
    this.initialize();
  }

  componentWillUnmount() {
    this.cm.toTextArea();
  }

  initialize() {
    this.cm = CodeMirror.fromTextArea(this.textarea, {
      lineNumbers: false,
      mode: languageToMode[this.language] || this.language,
      theme: "mdn-like"
    });

    this.setValue(this.props.file.content);

    this.cm.on(
      "change",
      cm => this.props.onChange(cm.getValue()) || (() => void 0)
    );

    this.cm.on(
      "blur",
      cm => this.props.onBlur(cm.getValue()) || (() => void 0)
    );

    this.cm.on(
      "focus",
      cm => this.props.onFocus(cm.getValue()) || (() => void 0)
    );

    if (this.props.focusOnMount) {
      this.focusStart();
    }
  }

  getValue() {
    return this.cm.getValue();
  }

  setValue(value) {
    this.cm.setValue(value);
  }

  setLanguage(language) {
    this.language = language;

    this.cm.toTextArea();
    this.initialize();
  }

  focusEnd() {
    this.cm.focus();
    this.cm.execCommand("goDocEnd");
  }

  focusStart() {
    this.cm.focus();
    this.cm.execCommand("goDocStart");
  }

  render() {
    return (
      <div className="CodeMirrorWriter-editor-container">
        <textarea ref={el => (this.textarea = el)} />
      </div>
    );
  }
}



