import React, { Component } from "react";
import "./Topbar.css";
import { Escapable } from "../ui/Escapable";
import { AutohidingSidebar } from "./AutohidingSidebar";

export class Topbar extends Component {
  constructor() {
    super();

    this.onKeyDown = this.onKeyDown.bind(this);
  }

  onKeyDown(e) {
    if (e.keyCode === 13) {
      this.props.onEnter();
    }
  }

  render() {
    return (
      <div className="Topbar">
        {this.props.expanded ? (
          <AutohidingSidebar
            expanded={this.props.expanded}
            onBlur={this.props.onBlur}
            focusablesPrefix="topbarFocusable"
          >
            <Escapable onEscape={this.props.onEscape}>
              <input
                id="topbarFocusableInput"
                className="Topbar-search-input"
                autoFocus
                onChange={this.props.onChangeSearchQuery}
                onKeyDown={this.onKeyDown}
                value={this.props.value}
              />
            </Escapable>
          </AutohidingSidebar>
        ) : null}
      </div>
    );
  }
}



