import React, { Component } from "react";
import { EditorView } from "prosemirror-view";
import { shouldComponentUpdateForFile } from "../helpers";
import { initState, initDoc, getTextFromDocument } from "./helpers";

export class ProseMirrorReader extends Component {
  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateForFile(this.props, nextProps);
  }

  render() {
    if (this.props.trim === true) {
      const doc = initDoc(this.props.file.content.doc);
      const content = getTextFromDocument(doc);

      return <div>{content}</div>;
    } else {
      const state = initState(this.props.file.content.doc, []);
      const view = new EditorView(document.createElement("div"), { state });
      const content = view.dom.innerHTML;
      view.destroy();

      return (
        <div className="ProseMirrorReader">
          <div dangerouslySetInnerHTML={{ __html: content }} />
        </div>
      );
    }
  }
}



