import React, { Component } from "react";
import "./Buttons.css";

class BasicButton extends Component {
  render() {
    return (
      <span
        className="Buttons-basic-button"
        onClick={this.props.onClick}
        onMouseDown={this.props.onMouseDown}
      >
        {this.props.children}
      </span>
    );
  }
}

export class AddButton extends Component {
  render() {
    return (
      <BasicButton
        onClick={this.props.onClick}
        onMouseDown={this.props.onMouseDown}
      >
        <i className="material-icons Buttons-15">add</i>
      </BasicButton>
    );
  }
}

export class ClearButton extends Component {
  render() {
    return (
      <BasicButton
        onClick={this.props.onClick}
        onMouseDown={this.props.onMouseDown}
      >
        <i className="material-icons Buttons-15">clear</i>
      </BasicButton>
    );
  }
}

export class EditButton extends Component {
  render() {
    return (
      <BasicButton
        onClick={this.props.onClick}
        onMouseDown={this.props.onMouseDown}
      >
        <i className="material-icons Buttons-20">edit</i>
      </BasicButton>
    );
  }
}



