import {
  docJsonFromString,
  initDoc,
  getTextFromDocument
} from "./editor/ProseMirror/helpers";

const fileExtensionToLanguage = {
  js: "javascript",
  html: "html",
  rt: "richtext",
  todo: "todo"
};

const fileExtensionsWhichAreCode = ["javascript", "html"];

export function normalizeLanguage(language) {
  if (fileExtensionsWhichAreCode.includes(language)) {
    return "code";
  }

  return language;
}

export function detectLanguage(fileName) {
  return fileExtensionToLanguage[fileName.split(".").pop()];
}

const converters = {
  "richtext -> todo": content => ({
    todos: getTextFromDocument(initDoc(content.doc))
      .split("\n")
      .map(text => ({ text, checked: false }))
  }),
  "richtext -> code": content => getTextFromDocument(initDoc(content.doc)),
  "todo -> richtext": content => ({
    doc: docJsonFromString(content.todos.map(({ text }) => text).join("\n"))
  }),
  "todo -> code": content => content.todos.map(({ text }) => text).join("\n"),
  "code -> richtext": content => ({
    doc: docJsonFromString(content)
  }),
  "code -> todo": content => ({
    todos: content.split("\n").map(text => ({ text, checked: false }))
  })
};

export function changeFileName(file, newName) {
  const oldLanguage = detectLanguage(file.name) || file.language;
  const newLanguage = detectLanguage(newName) || file.language;

  const oldLanguageNormalized = normalizeLanguage(oldLanguage);
  const newLanguageNormalized = normalizeLanguage(newLanguage);

  let content;

  if (newLanguageNormalized === oldLanguageNormalized) {
    content = file.content;
  } else {
    const converterDirective = `${oldLanguageNormalized} -> ${newLanguageNormalized}`;

    const newContent = converters[converterDirective](file.content);

    content = newContent;
  }

  return {
    ...file,
    name: newName,
    kind: "text",
    language: newLanguage,
    content
  };
}

export function getFileContent(file) {
  if (file.language === "richtext") {
    const doc = initDoc(file.content.doc);
    return getTextFromDocument(doc);
  } else if (file.language === "todo") {
    return file.content.todos.map(({ text }) => text);
  } else {
    return file.content;
  }
}

export function searchSnippet(snippet, query) {
  const upperCaseQuery = query.toUpperCase();

  let entries = []
    .concat(snippet.tags)
    .concat(snippet.title)
    .concat(snippet.files.map(file => file.name))
    .concat(snippet.files.map(getFileContent))
    .reduce((acc, array) => acc.concat(array), []); // flatten possible todo sub arrays

  return entries.some(text => text.toUpperCase().includes(upperCaseQuery));
}

export function replaceItemInListById(list, id, newItem) {
  return list.map(item => (item.id === id ? newItem : item));
}

export function replaceItemInListByIndex(list, index, newItem) {
  return list.map((item, _index) => (_index === index ? newItem : item));
}

export function dropItemInListById(list, id) {
  return list.filter(item => item.id !== id);
}

export function aggregateTags(snippets) {
  const tags = snippets.reduce((acc, snippet) => acc.concat(snippet.tags), []);

  return [...new Set(tags)];
}

export function copyToClipboard(str) {
  const el = document.createElement("textarea");
  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand("copy");
  document.body.removeChild(el);
}

export function bootstrapFile(filename, content, language) {
  let _content;

  if (language === "richtext") {
    _content = { doc: docJsonFromString(content) };
  } else if (language === "todo") {
    _content = { todos: [] };
  } else {
    _content = content;
  }

  return {
    name: filename,
    content: _content,
    language: language
  };
}

export function bootstrapSnippet(title, filename, content, language) {
  return {
    title: title,
    files: [bootstrapFile(filename, content, language)],
    tags: [],
    type: "textual"
  };
}

export function isStayingWithFocusables(e, focusablesPrefix) {
  if (
    !e.target ||
    !e.relatedTarget ||
    !e.target.id ||
    !e.relatedTarget.id ||
    !e.target.id.startsWith(focusablesPrefix) ||
    !e.relatedTarget.id.startsWith(focusablesPrefix)
  ) {
    return false;
  } else {
    return true;
  }
}



