import React, { Component } from "react";
import "./SnippetEditor.css";
import { EditableLabel } from "../ui/EditableLabel";
import {
  changeFileName,
  replaceItemInListByIndex,
  bootstrapFile
} from "../helpers";
import { getViewsForFile, getCssClassFile } from "./helpers";
import { AddButton, ClearButton } from "../ui/Buttons";

export class SnippetEditor extends Component {
  constructor(props) {
    super(props);

    this.onAddFile = this.onAddFile.bind(this);
    this.onChangeFile = this.onChangeFile.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onKeyDownInAddTag = this.onKeyDownInAddTag.bind(this);
    this.onBlurInAddTag = this.onBlurInAddTag.bind(this);
    this.onAddTag = this.onAddTag.bind(this);
    this.onChangeTagInput = this.onChangeTagInput.bind(this);
    this.onChangeHasFocus = this.onChangeHasFocus.bind(this);

    this.state = {
      snippet: props.snippet,
      currentFileIdx: 0,
      tagInput: "",
      editorHasFocus: null
    };
  }

  persist() {
    this.props.onSave && this.props.onSave(this.state.snippet);
  }

  onChangeTitle(title) {
    this.setState(oldState => {
      return {
        snippet: {
          ...oldState.snippet,
          title
        }
      };
    }, this.persist);
  }

  onUpdateFile(index, content, cb) {
    this.setState(oldState => {
      const oldFile = oldState.snippet.files[index];
      const files = replaceItemInListByIndex(oldState.snippet.files, index, {
        ...oldFile,
        content
      });

      const snippet = { ...oldState.snippet, files };

      return { snippet };
    }, cb);
  }

  onChangeFile(index, content) {
    this.onUpdateFile(index, content, () => undefined);
  }

  onBlurFile(index, content) {
    this.onUpdateFile(index, content, () => this.persist());
  }

  onSuggestPersist(index, content) {
    this.onUpdateFile(index, content, () => this.persist());
  }

  onChangeFileName(index, newName) {
    this.setState(oldState => {
      const files = oldState.snippet.files.map(
        (file, _index) =>
          index === _index ? changeFileName(file, newName) : file
      );

      return { snippet: { ...oldState.snippet, files } };
    }, this.persist);
  }

  onAddFile(e) {
    this.setState(oldState => {
      const newFile = bootstrapFile("", "", "richtext");
      const files = oldState.snippet.files.concat(newFile);

      return {
        snippet: { ...oldState.snippet, files },
        currentFileIdx: files.length - 1
      };
    }, this.persist);
  }

  onSelectFile(index) {
    this.setState({
      currentFileIdx: index
    });
  }

  onDeleteFile(index) {
    this.setState(oldState => {
      const files = oldState.snippet.files.filter(
        (file, _index) => index !== _index
      );

      const currentFileIdx = 0;

      return {
        snippet: { ...oldState.snippet, files },
        currentFileIdx
      };
    }, this.persist);
  }

  onKeyDownInAddTag(e) {
    if (e.keyCode === 13) {
      this.onAddTag();
    } else if (e.keyCode === 27) {
      e.target.blur();
    }
  }

  onBlurInAddTag(e) {
    this.setState({ tagInput: "" });
  }

  onAddTag() {
    this.setState(oldState => {
      const tags = oldState.snippet.tags.concat(this.state.tagInput);

      const uniqTags = Array.from(new Set(tags));

      return {
        tagInput: "",
        snippet: { ...oldState.snippet, tags: uniqTags }
      };
    }, this.persist);
  }

  onDeleteTag(index) {
    this.setState(oldState => {
      const tags = oldState.snippet.tags.filter(
        (tag, _index) => index !== _index
      );

      return {
        snippet: { ...oldState.snippet, tags }
      };
    }, this.persist);
  }

  onChangeTagInput(e) {
    this.setState({ tagInput: e.target.value });
  }

  onChangeHasFocus(editorHasFocus) {
    this.setState({ editorHasFocus });
  }

  render() {
    const file = this.state.snippet.files[this.state.currentFileIdx];
    const hasMoreThanOneFile = this.state.snippet.files.length > 1;

    const { Editor, Menu } = getViewsForFile(file);

    const fileClass = getCssClassFile(file);

    const classes = ["SnippetEditor"];

    classes.push(fileClass);

    classes.push(
      this.state.editorHasFocus
        ? "SnippetEditor-focused"
        : "SnippetEditor-not-focused"
    );

    const fileEditor = (
      <Editor
        // Key is needed in order to force the component to unmount and then mount again and
        // therefor call the componentDidMount callback to initialize non-react components
        key={`${this.state.currentFileIdx}-${
          this.state.snippet.files[this.state.currentFileIdx].name
        }`}
        file={file}
        onBlur={this.onBlurFile.bind(this, this.state.currentFileIdx)}
        onChange={this.onChangeFile.bind(this, this.state.currentFileIdx)}
        onSuggestPersist={this.onSuggestPersist.bind(
          this,
          this.state.currentFileIdx
        )}
        allowReadMode={this.props.allowReadMode}
        focusOnMount={this.props.focusOnMount}
        onChangeHasFocus={this.onChangeHasFocus}
      />
    );

    const fileTabs = this.state.snippet.files.map((file, index) => (
      <span
        key={`${file.name}${index}`}
        className={`SnippetEditor-file ${index === this.state.currentFileIdx &&
          hasMoreThanOneFile &&
          "SnippetEditor-file-selected"}`}
      >
        <span onClick={this.onSelectFile.bind(this, index)}>
          <EditableLabel
            value={file.name}
            substitute="untitled"
            placeholder="Set a filename"
            onChange={this.onChangeFileName.bind(this, index)}
          />
        </span>

        {hasMoreThanOneFile && (
          <ClearButton onClick={this.onDeleteFile.bind(this, index)} />
        )}
      </span>
    ));

    const tags = this.state.snippet.tags.map((tag, index) => (
      <span key={index} className="SnippetEditor-tag">
        <i className="material-icons">label</i>
        {tag}
        <ClearButton onClick={this.onDeleteTag.bind(this, index)} />
      </span>
    ));

    return (
      <div className={classes.join(" ")}>
        <div className="SnippetEditor-header">
          <div className="SnippetEditor-header-files">
            {hasMoreThanOneFile && (
              <span className="SnippetEditor-title">
                <EditableLabel
                  value={this.state.snippet.title}
                  substitute="..."
                  placeholder="Set a title"
                  onChange={this.onChangeTitle}
                />
              </span>
            )}
            {fileTabs}

            <AddButton onClick={this.onAddFile} />

            <span className="SnippetEditor-upper-right-corner">
              {this.props.upperRightCorner}
            </span>
          </div>

          <div className="SnippetEditor-header-menu">
            <Menu snippet={this.props.snippet} />
          </div>
        </div>

        <div className="SnippetEditor-file-view">{fileEditor}</div>

        <div className="SnippetEditor-footer">
          {tags}

          <input
            value={this.state.tagInput}
            placeholder=""
            onChange={this.onChangeTagInput}
            onKeyDown={this.onKeyDownInAddTag}
            onBlur={this.onBlurInAddTag}
            tabIndex={-1}
          />

          <AddButton onClick={this.onAddTag} />
        </div>
      </div>
    );
  }
}



