import React, { Component } from "react";
import "./TodoList.css";
import { isStayingWithFocusables } from "../../helpers";

function getOpenEntries(todos) {
  return todos.filter(todo => !todo.checked);
}

function getDoneEntries(todos) {
  return todos.filter(todo => todo.checked);
}

function updateTodoText(list, index, text) {
  return list.map(
    (item, _index) => (_index === index ? { ...item, text } : item)
  );
}

function updateTodoChecked(list, index, checked) {
  const openEntries = getOpenEntries(list).filter(
    (item, _index) => _index !== index
  );

  const doneEntries = getDoneEntries(list).filter(
    (item, _index) => _index + openEntries.length + (checked ? 1 : 0) !== index
  );

  const entry = { ...list[index], checked };

  const newList = openEntries.concat([entry]).concat(doneEntries);

  return newList;
}

function insertPlaceholderEntry(list) {
  if (list.length === 0 || list[0].checked) {
    // all todos have been completed, insert an empty todo at the beginning
    return [{ text: "", checked: false }].concat(list);
  }
  return list;
}

function getCaretPosition() {
  const selection = document.getSelection();
  return selection.focusOffset;
}

function convertCharacterCodes(stringWithCharacterCodes) {
  const element = document.createElement("span");
  element.innerHTML = stringWithCharacterCodes;
  return element.textContent;
}

class InputField extends Component {
  componentDidMount() {
    this.input.innerHTML = this.props.todo;

    if (!this.props.readonly && this.props.shouldFocus) {
      this.focus(this.props.cursorPosition);
    }
  }

  focus(position) {
    this.input.focus();

    if (this.input.textContent.length > 0) {
      const range = document.createRange();
      const sel = window.getSelection();

      range.setStart(this.input.childNodes[0], position);
      range.collapse(true);

      sel.removeAllRanges();
      sel.addRange(range);
    }
  }

  render() {
    return this.props.readonly ? (
      <div
        className="TodoList-entry-input-field"
        ref={ref => (this.input = ref)}
        spellCheck={false}
      />
    ) : (
      <div
        id={`todoListEntry-${this.props.index}`}
        className="TodoList-entry-input-field"
        contentEditable={true}
        suppressContentEditableWarning={true}
        onKeyDown={this.props.onKeyDown}
        onInput={this.props.onChange}
        onFocus={this.props.onFocus}
        onBlur={this.props.onBlur}
        ref={ref => (this.input = ref)}
      />
    );
  }
}

export class TodoList extends Component {
  constructor(props) {
    super(props);

    const todos = insertPlaceholderEntry(props.todos);

    this.state = {
      todos,
      currentlyEditedTodo: this.props.focusOnMount ? 0 : null,
      cursorPosition: 0,
      timestamp: Date.now()
    };

    this.onBlur = this.onBlur.bind(this);

    this.inputRefs = [];
  }

  componentDidUpdate(oldProps) {
    if (this.props.isEditing && !oldProps.isEditing) {
      this.inputRefs[0].focus();
    }
  }

  change() {
    this.props.onChange({ todos: this.state.todos });
  }

  blur() {
    this.props.onBlur({ todos: this.state.todos });
  }

  suggestPersist() {
    this.props.onSuggestPersist &&
      this.props.onSuggestPersist({ todos: this.state.todos });
  }

  focus() {
    this.props.onFocus({ todos: this.state.todos });
  }

  onKeyDown(index, e) {
    if (e.keyCode === 8) {
      // BACKSPACE

      if (this.state.todos[index].checked) {
        e.preventDefault();
        return;
      }

      const value = this.state.todos[index].text;

      if (getCaretPosition() === 0) {
        e.preventDefault();

        if (index === 0) {
          return;
        }

        if (value === "") {
          this.setState(
            oldState => {
              const newIndex = index - 1;

              return {
                todos: oldState.todos.filter(
                  (todo, _index) => _index !== index
                ),
                currentlyEditedTodo: newIndex,
                cursorPosition: oldState.todos[newIndex].text.length
              };
            },
            () => this.change()
          );
        } else {
          this.setState(
            oldState => {
              const newTodos = oldState.todos.filter(
                (todo, _index) => _index !== index
              );

              const newIndex = index - 1;

              return {
                todos: updateTodoText(
                  newTodos,
                  newIndex,
                  `${newTodos[newIndex].text}${value}`
                ),
                currentlyEditedTodo: newIndex,
                cursorPosition: newTodos[newIndex].text.length
              };
            },
            () => this.change()
          );
        }
      }
    } else if (e.keyCode === 13) {
      // ENTER
      e.preventDefault();

      if (this.state.todos[index].checked) {
        return;
      }

      const caretPosition = getCaretPosition();
      const content = convertCharacterCodes(this.state.todos[index].text);
      const firstPart = content.slice(0, caretPosition).trim();
      const secondPart = content.slice(caretPosition, content.length).trim();

      this.setState(
        oldState => {
          const todos = this.state.todos;

          const newTodos = todos
            .slice(0, index)
            .concat([
              { ...todos[index], text: firstPart },
              { text: secondPart }
            ])
            .concat(todos.slice(index + 1, todos.length));

          return {
            todos: newTodos,
            currentlyEditedTodo: index + 1,
            cursorPosition: null
          };
        },
        () => this.change()
      );
    } else if (e.keyCode === 38) {
      // UP ARROW
      if (getCaretPosition() === 0) {
        e.preventDefault();

        this.setState(
          oldState => {
            return {
              currentlyEditedTodo: Math.max(
                oldState.currentlyEditedTodo - 1,
                0
              ),
              cursorPosition: null
            };
          },
          () => {
            this.change();
            this.inputRefs[this.state.currentlyEditedTodo].focus();
          }
        );
      }
    } else if (e.keyCode === 40) {
      // DOWN ARROW
      if (
        getCaretPosition() ===
        convertCharacterCodes(this.state.todos[index].text).length
      ) {
        e.preventDefault();

        this.setState(
          oldState => {
            return {
              currentlyEditedTodo: Math.min(
                oldState.currentlyEditedTodo + 1,
                oldState.todos.length - 1
              ),
              cursorPosition: null
            };
          },
          () => {
            this.change();
            this.inputRefs[this.state.currentlyEditedTodo].focus();
          }
        );
      }
    }
  }

  onChange(index, e) {
    const value = e.target.innerHTML;

    this.setState(
      oldState => {
        return {
          todos: updateTodoText(oldState.todos, index, value)
        };
      },
      () => this.change()
    );
  }

  onCheck(index, e) {
    if (!this.props.isEditing) {
      return false;
    }

    const checked = !this.state.todos[index].checked;

    this.setState(
      oldState => {
        const newTodos = updateTodoChecked(oldState.todos, index, checked);

        return {
          todos: insertPlaceholderEntry(newTodos)
        };
      },
      () => this.suggestPersist()
    );
  }

  onFocus(index, e) {
    this.setState({ currentlyEditedTodo: index, cursorPosition: null }, () =>
      this.focus()
    );
  }

  onBlur(e) {
    if (!isStayingWithFocusables(e, "todoListEntry")) {
      this.blur();
    }
  }

  render() {
    const shouldFocus = index => this.state.currentlyEditedTodo === index;

    const renderInputField = (todo, index) => {
      return (
        <InputField
          index={index}
          todo={todo.text}
          onChange={this.onChange.bind(this, index)}
          onKeyDown={this.onKeyDown.bind(this, index)}
          onFocus={this.onFocus.bind(this, index)}
          shouldFocus={shouldFocus(index)}
          onBlur={this.onBlur}
          cursorPosition={this.state.cursorPosition}
          ref={ref => (this.inputRefs[index] = ref)}
          readonly={!this.props.isEditing}
        />
      );
    };

    const renderTodos = (todos, indexOffset, totalCount, showAsDone) => {
      return todos.map((todo, index) => (
        <div
          key={`${index + indexOffset}-${todos.length}-${totalCount}`}
          className={
            showAsDone ? "TodoList-entry TodoList-entry-done" : "TodoList-entry"
          }
        >
          <a
            id={`todoListEntry-checkbox-${this.props.index}`}
            className="TodoList-checkbox"
            type="checkbox"
            onClick={this.onCheck.bind(this, index + indexOffset)}
            disabled={!this.props.isEditing}
            tabIndex="0"
          >
            <i className="material-icons TodoList-checkbox-open-indicator">
              crop_square
            </i>
            <i className="material-icons TodoList-checkbox-done-indicator">
              done
            </i>
          </a>

          {renderInputField(todo, index + indexOffset)}
        </div>
      ));
    };

    const openEntries = getOpenEntries(this.state.todos);
    const renderedOpenEntries = renderTodos(
      openEntries,
      0,
      this.state.todos.length,
      false
    );

    const renderedDoneEntries = renderTodos(
      getDoneEntries(this.state.todos),
      openEntries.length,
      this.state.todos.length,
      true
    );

    // const entriesPlainText = this.state.todos.map((todo, index) => {
    //   return (
    //     <div key={index}>
    //       {index} / {todo.text} / {todo.checked ? "done" : "open"}
    //     </div>
    //   );
    // });

    // const isEditingIndicator = this.props.isEditing ? "editing" : "not editing";

    return (
      <div
        className={`TodoList ${
          this.props.isEditing ? "TodoList-editing" : "TodoList-read-only"
        }`}
        onClick={this.props.onClick}
      >
        {renderedOpenEntries}
        <br />
        {renderedDoneEntries}
      </div>
    );
  }
}



