import React, { Component } from "react";
import "./Sidebar.css";
import { CreateSnippetSidebar } from "./CreateSnippetSidebar";
import { TagsSidebar } from "./TagsSidebar";

export class Sidebar extends Component {
  render() {
    return (
      <span>
        <div className="Sidebar">
          <i
            tabIndex="0"
            id="sidebarFocusableAddButton"
            className="material-icons Sidebar-button"
            onClick={this.props.onClickAdd}
          >
            add_circle_outline
          </i>
          <i
            tabIndex="0"
            id="topbarFocusableSearchButton"
            className="material-icons Sidebar-button Sidebar-button-search"
            onClick={this.props.onClickSearch}
          >
            search
          </i>
          <i
            tabIndex="0"
            id="sidebarFocusableToggleTagsButton"
            className="material-icons Sidebar-button"
            onClick={this.props.onClickTags}
          >
            label
          </i>

          <hr />

          <i
            className="material-icons Sidebar-button Sidebar-button-vertical-split"
            onClick={() => this.props.setLayout(0)}
          >
            vertical_split
          </i>

          <i
            className="material-icons Sidebar-button"
            onClick={() => this.props.setLayout(1)}
          >
            view_stream
          </i>
          <hr />
        </div>

        <CreateSnippetSidebar
          onStartAddingNew={this.props.onStartAddingNew}
          expanded={this.props.createSnippetExpanded}
          onBlur={this.props.onBlur}
          onEscape={this.props.onEscape}
        />

        <TagsSidebar
          tags={this.props.tags}
          expanded={this.props.tagsExpanded}
          onBlur={this.props.onBlur}
          onEscape={this.props.onEscape}
          onClickTag={this.props.onClickTag}
          selectedTags={this.props.selectedTags}
        />
      </span>
    );
  }
}



