import React, { Component } from "react";
import { Escapable } from "../ui/Escapable";
import { AutohidingSidebar } from "./AutohidingSidebar";

class CreateSnippetButton extends Component {
  onKeyDown(e) {
    if (e.keyCode === 13) {
      // prevent default, otherwise a newline will be inserted to the snippet editors
      e.preventDefault();
      this.props.action(e);
    }
  }

  componentDidMount() {
    if (this.props.activate) {
      this.element.focus();
    }
  }

  render() {
    // We need to use anchors instead of buttons, because buttons dont have a relatedTarget property
    // on the click event in some browsers.
    return (
      <a
        id={`sidebarFocusable-${this.props.label}`}
        className={`ExpandableSidebar-create-snippet-button ExpandableSidebar-create-snippet-button-${
          this.props.classPostfix
        }`}
        onClick={this.props.action}
        onKeyDown={this.onKeyDown.bind(this)}
        tabIndex="0"
        ref={ref => (this.element = ref)}
      >
        <i className="material-icons">{this.props.iconCode}</i>
        {this.props.label}
      </a>
    );
  }
}

export class CreateSnippetSidebar extends Component {
  render() {
    return (
      <div className="ExpandableSidebar ExpandableSidebar-create-snippet">
        {this.props.expanded ? (
          <AutohidingSidebar
            onBlur={this.props.onBlur}
            focusablesPrefix="sidebarFocusable"
          >
            <Escapable onEscape={this.props.onEscape}>
              <CreateSnippetButton
                label="Code"
                classPostfix="code"
                iconCode="code"
                action={e =>
                  this.props.onStartAddingNew(e, {
                    language: "code"
                  })
                }
                activate={true}
              />

              <CreateSnippetButton
                label="Note"
                classPostfix="note"
                iconCode="note"
                action={e =>
                  this.props.onStartAddingNew(e, {
                    language: "richtext"
                  })
                }
              />

              <CreateSnippetButton
                label="Todos"
                classPostfix="todo"
                iconCode="assignment_turned_in"
                action={e =>
                  this.props.onStartAddingNew(e, {
                    language: "todo"
                  })
                }
              />

              <textarea
                id="sidebarFocusableCodeInput"
                placeholder="Paste Code..."
                className="ExpandableSidebar-create-snippet-input ExpandableSidebar-create-snippet-button-code"
                onChange={e =>
                  this.props.onStartAddingNew(e, {
                    type: "textual",
                    language: "code"
                  })
                }
                value=""
              />

              <textarea
                id="sidebarFocusableRichtextInput"
                placeholder="Paste Text..."
                className="ExpandableSidebar-create-snippet-input ExpandableSidebar-create-snippet-button-note"
                onChange={e =>
                  this.props.onStartAddingNew(e, {
                    type: "textual",
                    language: "richtext"
                  })
                }
                value=""
              />
            </Escapable>
          </AutohidingSidebar>
        ) : null}
      </div>
    );
  }
}



