import React, { Component } from "react";

import * as CodeMirror from "codemirror/lib/codemirror";
import "codemirror/addon/runmode/runmode.js";
import "codemirror/addon/runmode/colorize.js";

import { languageToMode } from "./helpers";
import { shouldComponentUpdateForFile } from "../helpers";

export class CodeMirrorReader extends Component {
  highlight() {
    CodeMirror.colorize([this.codeBlock]);
  }

  componentDidMount() {
    this.highlight();
  }

  componentDidUpdate(oldProps) {
    this.highlight();
  }

  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateForFile(this.props, nextProps);
  }

  trim(content) {
    return content
      .split("\n")
      .splice(0, 5)
      .join("\n");
  }

  render() {
    const language =
      languageToMode[this.props.file.language] || this.props.file.language;

    const content = this.props.trim
      ? this.trim(this.props.file.content)
      : this.props.file.content;

    return (
      <div className="cm-s-mdn-like">
        <pre ref={el => (this.codeBlock = el)} data-lang={language}>
          {content}
        </pre>
      </div>
    );
  }
}



