import React, { Component } from "react";
import { ClearButton } from "../ui/Buttons";
import "./SnippetLayout.css";
import { SnippetView } from "./SnippetView";

const loadingIndicatorForCreateAndDelete = (
  <div className="SnippetLayout-loading-indicator" />
);

const isCreatingOrDeleting = currentSnippetOperation =>
  currentSnippetOperation === "create" || currentSnippetOperation === "delete";

const isUpdating = currentSnippetOperation =>
  currentSnippetOperation === "update";

export class SnippetLayout extends Component {
  onClickDelete(snippet) {
    this.props.onClickDelete(snippet);
  }

  render() {
    const deleteButton = snippet => (
      <ClearButton onClick={this.onClickDelete.bind(this, snippet)} />
    );

    const updateLoadingIndicator = snippet =>
      this.props.currentlyLoadingSnippetId === snippet.id &&
      (isUpdating(this.props.currentSnippetOperation) ||
        isCreatingOrDeleting(this.props.currentSnippetOperation)) && (
        <span className="SnippetLayout-spinner">
          <i className="SnippetLayout-rotating material-icons Buttons-15">
            autorenew
          </i>
        </span>
      );

    const upperRightCorner = snippet => (
      <span>{updateLoadingIndicator(snippet) || deleteButton(snippet)}</span>
    );

    const createOrDeleteLoadingIndicator = snippet =>
      this.props.currentlyLoadingSnippetId === snippet.id &&
      isCreatingOrDeleting(this.props.currentSnippetOperation) &&
      loadingIndicatorForCreateAndDelete;

    const snippets = this.props.snippets.map(snippet => (
      <div key={snippet.id}>
        <div className="SnippetLayout-snippet-outer">
          <div className="SnippetLayout-snippet-inner">
            {createOrDeleteLoadingIndicator(snippet)}

            <SnippetView
              snippet={snippet}
              onSave={this.props.onSave}
              upperRightCorner={upperRightCorner(snippet)}
              allowReadMode={true}
              focusOnMount={true}
            />
          </div>
        </div>
        <hr />
      </div>
    ));

    return (
      <div className="SnippetLayout SnippetLayout-outer">
        <div className="SnippetLayout-inner">{snippets}</div>
      </div>
    );
  }
}



