import React, { Component } from "react";
import { AutohidingSidebar } from "./AutohidingSidebar";
import { Escapable } from "../ui/Escapable";
import "./TagsSidebar.css";

class TagEntry extends Component {
  componentDidMount() {
    this.props.activate && this.ref.focus();
  }

  onKeyDown(tag, e) {
    if (e.keyCode === 13) {
      this.props.onClick(tag);
    }
  }

  render() {
    return (
      <a
        id={`sidebarFocusable-${this.props.index}`}
        key={this.props.tag}
        className={`TagsSidebar-entry ${
          this.props.isSelected ? "TagsSidebar-entry-selected" : null
        }`}
        ref={ref => (this.ref = ref)}
        tabIndex={0}
        onClick={() => this.props.onClick(this.props.tag)}
        onKeyDown={this.onKeyDown.bind(this, this.props.tag)}
      >
        {this.props.tag}
      </a>
    );
  }
}

export class TagsSidebar extends Component {
  render() {
    const tags = this.props.tags.map((tag, index) => (
      <TagEntry
        key={tag}
        tag={tag}
        index={index}
        activate={index === 0}
        isSelected={this.props.selectedTags.includes(tag)}
        onClick={this.props.onClickTag}
      />
    ));

    return (
      <div className="ExpandableSidebar ExpandableSidebar-tags">
        {this.props.expanded ? (
          <AutohidingSidebar
            onBlur={this.props.onBlur}
            focusablesPrefix="sidebarFocusable"
          >
            <Escapable onEscape={this.props.onEscape}>{tags}</Escapable>
          </AutohidingSidebar>
        ) : null}
      </div>
    );
  }
}



