import React, { Component } from "react";
import { Modal } from "./ui/Modal";
import { normalizeLanguage, getFileContent, copyToClipboard } from "./helpers";
import { getReaderForFile } from "./editor/helpers";
import "./QuickSearch.css";

export class QuickSearch extends Component {
  constructor(props) {
    super(props);

    this.onQueryChange = this.onQueryChange.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);

    this.state = { filteredFiles: [], query: "", selectedSnippetIndex: 0 };
  }

  onQueryChange(e) {
    const query = e.target.value;

    const upperCaseQuery = query.toUpperCase();

    const enrichFileEntry = snippet => file => ({
      ...file,
      snippetTitle: snippet.title,
      textContent: getFileContent(file)
    });

    const extractFiles = snippet =>
      snippet.files
        .filter(({ language }) => normalizeLanguage(language) === "code")
        .map(enrichFileEntry(snippet));

    const fileEntryContainsQuery = ({ name, snippetTitle, textContent }) => {
      return [name, snippetTitle, textContent].some(text =>
        text.toUpperCase().includes(upperCaseQuery)
      );
    };

    const filteredFiles = this.props.snippets
      .filter(snippet => snippet.type === "textual")
      .map(extractFiles)
      .reduce((accArray, fileArray) => accArray.concat(fileArray), [])
      .filter(fileEntryContainsQuery);

    this.setState({
      query,
      filteredFiles: filteredFiles
    });
  }

  onKeyDown(e) {
    if (e.keyCode === 38) {
      // up arrow
      let idx = this.state.selectedSnippetIndex;
      e.preventDefault();
      idx--;
      if (idx < 0) {
        idx = this.state.filteredFiles.length - 1;
      }
      this.setState({ selectedSnippetIndex: idx });
    } else if (e.keyCode === 40) {
      // down arrow
      let idx = this.state.selectedSnippetIndex;
      e.preventDefault();
      idx++;
      if (idx >= this.state.filteredFiles.length) {
        idx = 0;
      }
      this.setState({ selectedSnippetIndex: idx });
    } else if (e.keyCode === 13) {
      // enter
      e.preventDefault();
      const content = getFileContent(
        this.state.filteredFiles[this.state.selectedSnippetIndex]
      );

      copyToClipboard(content);

      this.props.onClose();
    }
  }

  render() {
    const renderFileReader = file => {
      const Reader = getReaderForFile(file);

      return (
        <span className="QuickSearch-item">
          <Reader file={file} />
        </span>
      );
    };

    const renderedFilteredFiles =
      this.state.query !== "" &&
      this.state.filteredFiles.map((file, index) => (
        <div key={`${file.textContent}-${index}`}>
          <div
            className={
              this.state.selectedSnippetIndex === index
                ? "QuickSearch-item QuickSearch-seleted-item"
                : "QuickSearch-item"
            }
          >
            {renderFileReader(file)}
          </div>
          <hr />
        </div>
      ));

    return (
      <Modal onClose={this.props.onClose}>
        <div className="QuickSearch">
          <div className="QuickSearch-header">
            <input
              autoFocus
              onChange={this.onQueryChange}
              onKeyDown={this.onKeyDown}
              className="QuickSearch-input"
            />
          </div>

          <div>{renderedFilteredFiles}</div>
        </div>
      </Modal>
    );
  }
}



