import React, { Component } from "react";
import "./EditableLabel.css";

export class EditableLabel extends Component {
  constructor(props) {
    super(props);

    this.state = { isEditing: false, value: props.value };

    this.onStartEdit = this.onStartEdit.bind(this);
    this.onStopEdit = this.onStopEdit.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onKeyPress = this.onKeyPress.bind(this);
  }

  onStartEdit() {
    this.setState({ isEditing: true });
  }

  onStopEdit() {
    this.setState({ isEditing: false });

    this.props.onChange(this.state.value);
  }

  onChange($event) {
    const value = $event.target.value;

    this.setState({ value });
  }

  onKeyPress(e) {
    if (e.key === "Enter") {
      this.onStopEdit();
    }
  }

  render() {
    const input = (
      <input
        className="EditableLabel-write"
        value={this.state.value}
        onChange={this.onChange}
        onBlur={this.onStopEdit}
        onKeyPress={this.onKeyPress}
        placeholder={this.props.placeholder}
        autoFocus
      />
    );

    const readModeClasses = ["EditableLabel-read"];

    if (!this.state.value) {
      readModeClasses.push("EditableLabel-read-placeholder");
    }

    const span = (
      <span
        className={readModeClasses.join(" ")}
        onDoubleClick={this.onStartEdit}
      >
        {this.state.value || this.props.substitute || "empty"}
      </span>
    );

    return this.state.isEditing ? input : span;
  }
}



