import { Plugin } from "prosemirror-state";
import { lift, toggleMark, setBlockType } from "prosemirror-commands";
import { wrapInList } from "prosemirror-schema-list";
import { mySchema } from "./helpers";

class MenuView {
  constructor(items, editorView) {
    this.items = items;
    this.editorView = editorView;

    this.dom = document.createElement("div");
    this.dom.className = "menubar";
    items.forEach(({ dom }) => this.dom.appendChild(dom));
    this.update();

    setTimeout(() => this.dom.classList.add("visible"), 10);

    this.dom.addEventListener("mousedown", e => {
      e.preventDefault();
      editorView.focus();
      items.forEach(({ command, dom }) => {
        if (dom.contains(e.target))
          command(editorView.state, editorView.dispatch, editorView);
      });
    });
  }

  update() {
    this.items.forEach(({ command, dom }) => {
      let active = command(this.editorView.state, null, this.editorView);
      dom.style.display = active ? "" : "";
    });
  }

  destroy() {
    this.dom.remove();
  }
}

function menuPlugin(items) {
  return new Plugin({
    view(editorView) {
      const menuView = new MenuView(items, editorView);

      const toolbarContainer = editorView.dom
        .closest(".SnippetEditor")
        .querySelector(".proseMirrorToolbarContainer");

      const toolbar = toolbarContainer.querySelector("div.menubar");

      if (toolbar) {
        toolbar.remove();
      }

      toolbarContainer.appendChild(menuView.dom);

      return menuView;
    }
  });
}

function newIcon(text, demo) {
  let span = document.createElement("span");
  span.className = "ProseMirrorWrapper-menu-item";
  span.title = text;
  span.innerHTML = demo || text;
  return span;
}

function heading(level, text, demo) {
  return {
    command: setBlockType(mySchema.nodes.heading, { level }),
    dom: newIcon(text, demo)
  };
}

function wrapListItem(nodeType, options) {
  return wrapInList(nodeType);
}

export const menu = menuPlugin([
  {
    command: toggleMark(mySchema.marks.strong),
    dom: newIcon("bold", "<strong>bold</strong>")
  },
  {
    command: toggleMark(mySchema.marks.em),
    dom: newIcon("italic", "<i>italic</italic>")
  },
  {
    command: setBlockType(mySchema.nodes.paragraph),
    dom: newIcon("body", "body")
  },
  heading(2, "Title", "<h2 style='display: inline;'>Title</h2>"),
  heading(3, "Subtitle", "<h3 style='display: inline;'>Subtitle</h3>"),
  {
    command: wrapListItem(mySchema.nodes.bullet_list),
    dom: newIcon(
      "bullets",
      "<i class='material-icons ProseMirrorWrapper-menu-item-icon'>format_indent_increase</i>"
    )
  },
  {
    command: lift,
    dom: newIcon(
      "lift",
      "<i class='material-icons ProseMirrorWrapper-menu-item-icon'>format_indent_decrease</i>"
    )
  }
]);



