export class UserSession {
  constructor(backend) {
    this.backend = backend;
  }

  getUserIdFromLocalStorage() {
    return new Promise((resolve, reject) =>
      resolve(localStorage.getItem("userId"))
    );
  }

  storeUserIdInLocalStorage(user) {
    return new Promise((resolve, reject) => {
      localStorage.setItem("userId", user.id);
      resolve(user);
    });
  }

  getUser() {
    return new Promise((resolve, reject) => {
      const createUser = () =>
        this.backend
          .createUser()
          .then(this.storeUserIdInLocalStorage)
          .then(resolve);

      this.getUserIdFromLocalStorage().then(userId => {
        if (!userId) {
          createUser();
        } else {
          this.backend
            .getUser(userId)
            .then(resolve)
            .catch(() => {
              createUser();
            });
        }
      });
    });
  }
}



